import React from 'react'

const Persons = ({ personsToShow, handleDeleteClick }) => {
  return (
    <div>
      {personsToShow.map((person, i) =>
        <p key={person.id}>
          {person.name} {person.number} <button 
                                          onClick={handleDeleteClick}
                                          value={person.id}
                                        >delete</button>
        </p>
      )}
    </div>
  )
}

export default Persons