import React, { useState, useEffect } from 'react'
import './index.css'
import Filter from './components/Filter'
import PersonForm from './components/PersonForm'
import Persons from './components/Persons'
import Notification from './components/Notification'
import personService from './services/persons'

const App = () => {
  const [ persons, setPersons ] = useState([]) 
  const [ newName, setNewName ] = useState('')
  const [ newNumber, setNewNumber ] = useState('')
  const [ personsToShow, setPersonsToShow ] = useState([])
  const [ filter, setFilter ] = useState('')
  const [ message, setMessage ] = useState({type: null, text: null})

  // Get phonebook data from server using effect hook
  useEffect(() => {
    personService
      .getAll()
      .then(initialPersons => {
        setPersons(initialPersons)
        setPersonsToShow(initialPersons)
      })
  }, [])
  
  const addPerson = (event) => {
    event.preventDefault()

    // New person is added to phonebook only if s/he doesn't exist in there yet.
    // Convert both names to lower case in order to ensure the same format.  
    if (persons.find( ({ name }) => name.toLowerCase() === newName.toLowerCase() ) === undefined ) {

      // Both input fields needs to be filled
      if (newName !== '' && newNumber !== '') {

        const personObject = {
          name: newName,
          number: newNumber
        }
        
        personService
          .create(personObject)
          .then(createdPersons => {
            // Display info message
            const messageObject = {...message, type: 'info', text: `Added record '${newName}'`}
            setMessage(messageObject)
            setTimeout(() => {
              setMessage({type: null, text: null})
            }, 3000)

            // Update persons and persons-to-show states
            setPersons(persons.concat(createdPersons))
            setPersonsToShow(persons.concat(createdPersons))

            // Reset all input fields
            setFilter('')
            setNewName('')
            setNewNumber('')
          })
          .catch(error => {
            // Display alert including error message if
            // backend returns input validation error.
            const messageObject = {...message, type: 'error', text: error.response.data.error}
            setMessage(messageObject)
            setTimeout(() => {
              setMessage({type: null, text: null})
            }, 3000)
          })

      // Missing input details -> no update, raise alert.
      } else {
        alert('You need to provide both name and number.')
      }

    // Name exists in phonebook already -> ask update. 
    } else {

      // Confirm update.
      const result = window.confirm(
          `${newName} is already added to phonebook, replace the old number with a new one?`
        )
      
      // User confirmed update -> make HTTP PUT request in order to update data on server.
      if (result) {

        // Get person object that is wanted to be updated.
        const person = persons.find(({name}) => name.toLowerCase() === newName.toLowerCase())
        const updatedPerson = { ...person, number: newNumber }

        personService
          .update(person.id, updatedPerson)
          .then(returnedPerson => {
            // Display info message
            const messageObject = {...message, type: 'info', text: `Updated record '${newName}'`}
            setMessage(messageObject)
            setTimeout(() => {
              setMessage({type: null, text: null})
            }, 3000)

            // Take a copy of current person array in order to avoid mutating the state directly.
            const arr = [...persons]

            // Update details in person array and reset all input fields.
            const index = arr.indexOf(person)
            if (index > -1 ) {
              arr[index] = updatedPerson
              setPersons(arr)
              setPersonsToShow(arr)
              setFilter('')
              setNewName('')
              setNewNumber('') 
            }
          })
          .catch(error => {
            const messageObject = {...message, type: 'error', text: `Record '${newName}' has already been removed from server'`}
            setMessage(messageObject)
            setTimeout(() => {
              // Clear error message after 3 seconds
              setMessage({type: null, text: null})

              // Delete entry, that was removed from the server, also from local list.
              const arr = [...persons]
              const index = arr.indexOf(person)
              if (index > -1 ) { 
                arr.splice(index, 1)
                setPersons(arr)
                setPersonsToShow(arr)
                setFilter('')
                setNewName('')
                setNewNumber('') 
              }
            }, 3000)
          })
      } 
    }
  }

  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  }

  const handleFilterChange = (event) => {
    const term = (event.target.value)
    setFilter(term)
    
    // Apply filter based on given term and form a new filtered array.
    const filteredList = persons.filter( ({ name }) => name.toLowerCase().startsWith(term.toLowerCase()) )
    setPersonsToShow(filteredList) 
  }

  const handleDeleteClick = (event) => {    
    
    // Get person object that is wanted to delete.
    const deletePerson = persons.find(({id}) => id === event.target.value)
    
    // Confirm delete from user.
    const result = window.confirm(`Delete ${deletePerson.name}?`)
    
    // User confirmed delete -> make HTTP DELETE request and update persons list.
    if (result) {
      personService
        .remove(deletePerson.id)
        .then(response => {          
          // Display info message
          const messageObject = {...message, type: 'info', text: `Removed record '${deletePerson.name}'`}
          setMessage(messageObject)
          setTimeout(() => {
            setMessage({type: null, text: null})
          }, 3000)
          
          const arr = [...persons]
          const index = arr.indexOf(deletePerson)
          if(index > -1) { 
            arr.splice(index, 1)
            setPersons(arr)
            setPersonsToShow(arr) 
          }
        })
    } 
  }

  return (
    <div>
      <h1>Phonebook</h1>
      <Notification message={message} />
      <Filter
        filter={filter}
        handleFilterChange={handleFilterChange} 
      />
      <h2>add a new</h2>
      <PersonForm 
        addPerson={addPerson} 
        newName={newName} 
        handleNameChange={handleNameChange} 
        newNumber={newNumber}
        handleNumberChange={handleNumberChange}
      />
      <h2>Numbers</h2>
      <Persons 
        personsToShow={personsToShow} 
        handleDeleteClick={handleDeleteClick}
      />
    </div>
  )
}

export default App